.flex {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
main {
  width: 1560px;
  margin: 0 auto;
  padding-top: 112px;
  background-color: #E5E5E5;
}
.main__container {
  width: 1320px;
  margin: 0 auto;
}
.main__content {
  margin-top: 88px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.steps {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.steps__step {
  width: 440px;
  border-top: 3px solid #D5D7DC;
  position: relative;
  padding-top: 32px;
}
.steps__step::before {
  position: absolute;
  top: -10px;
  left: 0;
  content: "";
  width: 16px;
  height: 16px;
  background-color: #D5D7DC;
  border-radius: 50%;
}
.steps__step:last-child::after {
  position: absolute;
  top: -10px;
  right: 0;
  content: "";
  width: 16px;
  height: 16px;
  background-color: #D5D7DC;
  border-radius: 50%;
}
.steps__step--active::before {
  background-color: #FFB300;
}
.steps__title {
  margin: 0;
  font-weight: normal;
  font-size: 18px;
}
.steps__subtitle {
  margin: 0;
  margin-top: 8px;
  font-size: 13px;
  line-height: 17px;
}
.card {
  width: 640px;
  background-color: #FFFFFF;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  border: 1px solid #F1F3F8;
}
.card__header {
  width: 560px;
  padding-top: 64px;
  padding-bottom: 64px;
  margin: 0 auto;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-bottom: 1px solid #E8E8E8;
}
.card__title-box {
  padding-left: 200px;
  padding-top: 24px;
  position: relative;
}
.card__title-box::before {
  position: absolute;
  content: "";
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background-color: #F8F9FC;
  top: 24px;
  left: 96px;
  background-image: url("/img/cash.svg");
  background-repeat: no-repeat;
  background-position: 23px 20px;
}
.card__title {
  margin: 0;
  margin-bottom: 8px;
  font-size: 30px;
  line-height: 40px;
  color: #343A47;
  font-weight: normal;
}
.card__subtitle {
  font-size: 13px;
  line-height: 17px;
  color: #9098AD;
}
.card__content {
  padding-top: 136px;
  padding-left: 64px;
  padding-bottom: 80px;
  padding-right: 64px;
}
.card__bold {
  font-weight: bold;
}
.card__button-box {
  margin-top: 48px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.card__agreement {
  margin-top: 48px;
}
.card__paragraph {
  margin: 0;
}
.card__paragraph:last-of-type {
  margin-top: 24px;
}
.card__agreement {
  font-size: 13px;
  line-height: 17px;
}
.button {
  width: 248px;
  border: none;
  background-color: #4C82FF;
  box-shadow: 0px 16.9257px 16.9257px rgba(45, 102, 235, 0.13), 0px 4.23142px 8.46285px rgba(45, 105, 235, 0.12), 0px -2.11571px 5.28928px rgba(76, 132, 255, 0.05);
  border-radius: 52px;
  color: #FFFFFF;
  line-height: 27px;
  padding-left: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  position: relative;
  text-align: left;
}
.button::before {
  position: absolute;
  width: 35px;
  height: 35px;
  content: "";
  background: rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  right: 6px;
  top: 6px;
  background-image: url("/img/arrow.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.link {
  width: 248px;
  color: #467BF6;
  background: #F8F9FC;
  border-radius: 24px;
  line-height: 27px;
  padding-left: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: left;
  text-decoration: none;
}
.agreement__checkbox {
  padding-left: 37px;
}
.agreement__label {
  color: #9098AD;
  position: relative;
}
.agreement__label::before {
  content: "";
  display: block;
  position: absolute;
  left: -37px;
  width: 18px;
  height: 18px;
  background-color: #467BF6;
  border-radius: 5px;
}
.agreement__input:checked + .agreement__label::before {
  background-image: url("/img/checkbox-tick.svg");
  background-repeat: no-repeat;
  background-position: 50%;
}
.agreement__input:focus + .agreement__label::before {
  outline: auto;
}
.agreement__link {
  color: #9098AD;
  text-decoration: underline;
}
.sign-up__title-box {
  padding-top: 24px;
  padding-left: 40px;
  padding-right: 40px;
}
.sign-up__subtitle {
  margin-top: 8px;
}
.form {
  border: none;
  padding-top: 126px;
  padding-left: 40px;
  padding-right: 40px;
}
.form__group {
  border: none;
  padding: 0;
}
.form__item {
  margin: 0;
  margin-bottom: 96px;
  position: relative;
}
.form__item:last-of-type {
  margin-bottom: 0;
}
.form__item::before {
  position: absolute;
  content: "";
  border: 2px solid #D5D7DC;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  bottom: 10px;
  right: 0;
}
.form__label {
  color: #9098AD;
  line-height: 27px;
}
.form__text {
  width: 100%;
  margin-top: 12px;
  height: 44px;
  border: none;
  border-bottom: 2px solid #D5D7DC;
}
.form__button {
  margin-top: 72px;
  margin-left: 180px;
}
